.container-div {
  height: 150px;
  margin-top: 40px !important;
  background-color: rgba(184, 184, 184, 0.39);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 4px 4px 4px rgb(206, 206, 206);
}

.input-btn {
  width: 50% !important;
  margin-top: 10px;
}

.btn-color {
  background-color: #139b4d !important;
  color: white !important;
  font-weight: 800 !important;
}

.input-tasks {
  margin-top: 50px;
  margin-left: 50px;
}

.containerClean {
  display: flex;
  flex-direction: row;
  width: 50%;
}

.containerClean p {
  font-size: 12px;
  font-weight: 800;
  margin-top: 12px;
  padding-right: 8px;
}

.txt-button {
  cursor: pointer;
  color: rgb(77, 77, 255);
}

.txt-button:hover {
  color: rgb(255, 97, 97);
}

h3 {
  font-weight: bold;
}

.readItem {
  border: rgba(128, 128, 128, 0.507);
  border-radius: 5px;
  width: 100%;
  font-family: 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 14px;
}